
import {
  IonContent,
  IonHeader,
  IonPage,
  IonTitle,
  IonToolbar,
  IonButtons,
  loadingController,
  IonBackButton,

} from "@ionic/vue";
import { defineComponent } from "vue";
import { useRoute, useRouter } from "vue-router";

export default defineComponent({
  name: "HomePage",
  components: {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar,
    IonButtons,
    IonBackButton
  },
   setup() {
    const route = useRoute();
    const router = useRouter();
    return { route, router };
  },
  data() {
    return {
      imageSrc: "",
      meals: "",
      origine:"",
      category:"",
      instructions:"",
      ingredients: [{}],
    };
  },
  ionViewDidEnter() {
    console.log("Home page did Enter");
    this.getJSON();
  },
  methods: {
    async getJSON() {
      const loading = await loadingController.create({
        message: "Attendre SVP...",
      });
      await loading.present();
      
      let url = `https://www.themealdb.com/api/json/v1/1/lookup.php?i=${this.route.params.id}`;
      fetch(url)
        .then((reponse) => reponse.json())
        .then((data) => {
          console.log(data);
          let temp: string[] = [];

          this.meals = data.meals[0].strMeal;
          this.origine = data.meals[0].strArea;
          this.category = data.meals[0].strCategory;
          this.imageSrc = data.meals[0].strMealThumb;
          this.instructions = data.meals[0].strInstructions;
          for (let i = 1; i < 21; i++) {
            var mesure = data.meals[0][`strMeasure${i}`];
            var ingred = data.meals[0][`strIngredient${i}`];
              if (ingred.length > 0 && mesure.length > 0 ) {
                temp.push(mesure + " " + ingred);
                this.ingredients = temp;
              }
          }
         
          loading.dismiss();
        });
    },
  },
});
