
import {
  IonContent,
  IonHeader,
  IonPage,
  IonTitle,
  IonToolbar,
  IonAvatar,
  IonItem,
  IonLabel
 
} from "@ionic/vue";
import { defineComponent } from "vue";
import { useRoute, useRouter } from "vue-router";



export default defineComponent({
  components: {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar,
    IonItem,
    IonAvatar,
    IonLabel
  },
  setup() {
    const route = useRoute();
    const router = useRouter();
    return { route, router };
  },
   data() {
    return {
      recettes: [{}]
    };
  },
  ionViewDidEnter() {
    console.log(this.route.params.id);
    this.getJSON();
  },
  methods: {
    async getJSON() {
      let url = `https://www.themealdb.com/api/json/v1/1/filter.php?c=${this.route.params.id}`;
      fetch(url)
        .then((reponse) => reponse.json())
        .then((data) => {
          console.log(data);
          let temp: object[] = [];

          for(let i= 0; i < data.meals.length; i++) {
            temp.push({id:data.meals[i].idMeal, strMeal: data.meals[i].strMeal, strMealThumb: data.meals[i].strMealThumb})
          }
          this.recettes = temp;
          
        });
    },

      afficheRecette(id:number) {
        this.router.push('/DetailsRecette/' + String(id));
      }
 }

});


